.contentUserFormBox {
  display: flex;
  width: 100%;

  .contentUserForm {
    display: flex;
    flex-direction: column;
    width: 50%;
    background-color: $grey;
    opacity: 0.95;
    padding: 2.5rem 2rem 0 3.5rem;

    h2 {
      color: $lighter-grey;
      margin-bottom: 1rem;
      text-transform: uppercase;
    }

    .neededFields {
      color: $white;
    }

    #add-plant,
    #add-material,
    #plan-work {
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      font-size: 1.2rem;
      width: 100%;      
      margin-top: 1rem;

      label {
        width: 50%;
        margin-bottom: 1rem;

        span {
          color: $lighter-grey;
          display: flex;
          width: 100%;
          margin-bottom: 0.4rem;
        }

        input,
        select {
          width: 15vw;
          padding: 0.3rem;
          font-size: 1.2rem;
        }

        input[type="date"],
        input[type="time"] {
          padding: 0.2rem;
        }
      }

      .labelForInputFile {
        color: $lighter-grey;
        span {
          color: $strong-grey;
          border: none;
          width: 5vw;
          height: 2.4rem;
          margin-top: 0.6rem;
          font-size: 1.1rem;
          font-weight: bold;
          padding: 0.5rem 5vw;
          background: $lighter-grey;
          cursor: pointer;
          width: 15vw;
        }

        .inputFile {
          display: none;
        }
      }

      button {
        color: $strong-grey;
        border: none;
        width: 90%;
        height: 3.4rem;
        margin-top: 1rem;
        font-size: 1.2rem;
        font-weight: bold;
        background: $lighter-grey;
        padding: 1rem 0;

        &:hover {
          cursor: pointer;
          opacity: 0.7;
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .contentUserFormBox .contentUserForm {
    #add-plant,
    #add-material,
    #plan-work {
      flex-direction: column;

      label {
        width: 90%;

        input {
          width: 90%;
        }
      }

      button {
        margin-bottom: 1.5rem;
      }
    }
  }

  .userSection .contentUser .existed-box {
    height: 95%;
  }
}

@media screen and (max-width: 950px) {
  .contentUserFormBox {
    flex-direction: column;

    .contentUserForm {
      width: 100%;
      #add-plant,
      #add-material,
      #plan-work {
        width: 100%;
        flex-direction: row;
        label {
          width: 15.5rem;
        }

        button {
          width: 14rem;
        }
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .contentUserFormBox .contentUserForm {
    width: 100%;
    #add-plant,
    #add-material,
    #plan-work {
      label {
        width: 90%;

        select {
          width: 90%;
        }
      }

      button {
        width: 81%;
        margin-top: 1rem;
        margin-bottom: 2rem;
      }
    }
  }
}

@media screen and (max-width: 550px) {
  .userSection .contentUser .existed-box ol li {
    flex-direction: row;
  }
}
