.content {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;

  .top-box {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  .plus-link {
    align-self: flex-end;
    font-size: 1.2rem;
    color: $strong-green;
    text-decoration: none;
    font-weight: bold;

    &:hover {
      cursor: pointer;
      opacity: 0.7;
    }
  }
  p {
    font-size: 1.2rem;
  }
  .contentList {
    width: 100%;
    display: flex;
    flex-direction: column;

    table {
      background-color: $limone;
      font-size: 1.2rem;
      margin-top: 1.2rem;
      padding: 0.6rem 0.6rem 1.1rem 0.6rem;
      height: 45vh;
      display: block;
      overflow-y: scroll;
      width: 100%;

      tr {
        background: rgba(255, 255, 255, 0.3);
        color: #444;
        th {
          padding: 0.3rem;
          width: 10%;
        }
        td {
          padding: 0.3rem;

          h3 {
            text-transform: uppercase;
            margin-bottom: 1rem;
          }

          svg:hover {
            cursor: pointer;
            opacity: 0.7;
          }

          a {
            color: $grey;

            &:hover {
              opacity: 0.7;
            }
          }
        }

        .shortTd {
          width: 3%;
        }

        .longTd {
          width: 15%;
        }
      }

      tr:first-child {
        background: rgba(255, 255, 255, 0);
        &:hover {
          background: rgba(255, 255, 255, 0);
        }
      }

      tr:nth-child(2) {
        background: rgba(255, 255, 255, 0.7);
        &:hover {
          background: rgba(255, 255, 255, 0.7);
        }
      }

      .workDone {
        background: $strong-green;
        color: $white;

        td a {
          color: $white;
        }

        &:hover {
          color: $strong-green;

          td a {
            color: $strong-green;
          }
        }
      }

      .workNotDone {
        background: $orange;
        color: $white;

        td a {
          color: $white;
        }

        &:hover {
          color: $orange;

          td a {
            color: $orange;
          }
        }
      }

      .summaryTr {
        font-weight: bold;
        td {
          padding: 1rem 0;
        }
        &:hover {
          color: $grey;
          background: rgba(255, 255, 255, 0);
        }
      }
    }
    .summaryBox {
      position: absolute;
      bottom: 1.5rem;
      font-weight: bold;
      background-color: $white;
      width: 96%;
      padding: 0.5rem;
      font-size: 1.2rem;
      display: flex;
      justify-content: flex-end;

      h3 {
        margin-right: 1rem;
      }
    }
  }

  .contentList table {
    tr:hover {
      color: #000;
      background: rgba(255, 255, 255, 0.7);
    }
  }
  .contentEdit table {
    tr:first-child {
      background: rgba(255, 255, 255, 0);
    }
    tr:nth-child(2) {
      background: rgba(255, 255, 255, 0.7);
    }
  }
}

@media screen and (max-width: 1210px) {
  .content .plus-link {
    position: static;
    top: 0;
    margin: 1.5rem 0;
  }
  .content table {
    padding: 0.6rem 0 1.1rem 0;
  }
}

@media screen and (max-width: 950px) {
  .content .top-box {
    flex-direction: column;

    h2 {
      margin-bottom: 1.5rem;
    }
  }

  .content .existed-plant-table {
    tr {
      th:nth-child(9) {
        display: none;
      }
      td:nth-child(10) {
        display: none;
      }
    }
  }
  .content table {
    tr {
      th:nth-child(5),
      th:nth-child(6),
      th:nth-child(7),
      th:nth-child(8) {
        display: none;
      }

      td:nth-child(5),
      td:nth-child(6),
      td:nth-child(7),
      td:nth-child(8) {
        display: none;
      }

      td h3 {
        margin-left: 1rem;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .content table {
    tr {
      td {
        padding: 0;
      }
      th:nth-child(4) {
        display: none;
      }

      td:nth-child(4) {
        display: none;
      }
    }
  }
  .content .existed-plant-table {
    tr {
      th:nth-child(5) {
        display: none;
      }
      td:nth-child(5) {
        display: none;
      }
    }
  }
}
